import { useTranslations } from 'next-intl';
import ProductionProcess from '@/components/ProductionProcess';
import BookingCalendar from '@/components/BookingCalendar';
import BaubleConfigurator from '@/components/BaubleConfigurator';

export default function HomePage() {
    const t = useTranslations('HomePage');

    return (
        <>
            <main className="flex min-h-screen flex-col items-center justify-between p-24 relative overflow-hidden">
                {/* Background Image */}
                <div
                    className="absolute inset-0 z-0 bg-cover bg-center bg-no-repeat"
                    style={{ backgroundImage: "url('/hero-background.png')" }}
                >
                    <div className="absolute inset-0 bg-black/60" /> {/* Dark overlay for readability */}
                </div>

                <div className="z-10 w-full max-w-5xl items-center justify-between font-mono text-sm lg:flex">
                    <h1 className="text-4xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-red-500 to-amber-500">
                        {t('title')}
                    </h1>
                    <div className="mt-8">
                        <h2 className="text-2xl">{t('hero.headline')}</h2>
                        <p className="mt-4 text-gray-600">{t('hero.subheadline')}</p>
                        <button className="mt-6 px-6 py-3 bg-red-600 text-white rounded-full hover:bg-red-700 transition-colors">
                            {t('hero.cta')}
                        </button>
                    </div>
                </div>

                {/* Scroll Indicator */}
                <div className="absolute bottom-10 animate-bounce text-gray-400">
                    <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 14l-7 7m0 0l-7-7m7 7V3" />
                    </svg>
                </div>
            </main>
            <ProductionProcess />
            <BookingCalendar />
            <BaubleConfigurator />
        </>
    );
}
